	export default {
		name: 'tabbar',
        data() {
            return {
                tabList: [
                    {
                        icon: 'mdi-home',
                        text: '首页',
                    },
                    {
                        icon: 'mdi-clock-outline',
                        text: '家人',
                    },
                    {
                        icon: 'mdi-room-service-outline',
                        text: '服务',
                    },
                    {
                        icon: 'mdi-account-circle',
                        text: '我的',
                    }
                ]
            }
        },
		props: {
			backgroundColor: { 
				type: String, 
				default: '#fbfbfb' ,
			},
			color: { 
				type: String, 
				default: '#999' ,
			},
			tintColor: { 
				type: String, 
				default: '#00aaef' ,
			}
		},
		computed: {
			currentTabIndex () {
				return this.$store.state.app.tabbarIndex
			}
		},
		methods: {
			switchTab(index){
				if(this.currentTabIndex===index) {
					return
				}
				this.$store.commit('SET_SINGLE_STATE', ['tabbarIndex', index])
				const tabbarUrls = [
					'../index/index',
					'../family/family',
					'../service/service',
					'../mine/mine',
					
				]
				uni.navigateTo({
					
					url: tabbarUrls[index]
				})
			}
		},
	}
